.buttonMore {
  width: 100%;
  background: rgba(20, 20, 131, 0.863);
  color: white;
  border: none;
  padding: 15px 30px;
  cursor: pointer;
}

.buttonMore:hover {
  width: 100%;
  background: rgb(6, 6, 54);
  color: white;
  border: none;
  padding: 15px 30px;
  cursor: pointer;
}

.buttonMore:disabled {
  background: #888;
  cursor: not-allowed;
}